
html,
body {
  width: 100%;
  margin: 0;
}
#app {
  margin: 0 auto;
  width: 80%;
  min-height: 100%;
}
#app .image-gallery {
  width: 200px;
  height: 500px;
  margin: 0 auto;
}
#app .item {
  height: 120px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 2px 0;
  text-align: center;
}
#app .img-item {
  height: 100%;
  max-width: 100%;
}

.vertical-gallery {
  position: relative;
  height: 100%;
  color: #666;
}
.vertical-gallery .vertical-next,
.vertical-gallery .vertical-prev {
  position: absolute;
  left: 0;
  width: 100%;
  height: 15px;
  background: #000;
  opacity: .5;
  cursor: pointer;
  z-index: 9;
}
.vertical-gallery .vertical-next:hover,
.vertical-gallery .vertical-prev:hover {
  opacity: 1;
}
.vertical-gallery .vertical-next {
  bottom: 0;
}
.vertical-gallery .vertical-prev {
  top: 0;
}
.vertical-gallery .next-span,
.vertical-gallery .prev-span {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 5px;
  background: url(../../static/img/arrow.8d98ad8.png) no-repeat;
  background-size: 100%;
}
.vertical-gallery .next-span {
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.vertical-gallery .prev-span {
  -webkit-transform: translate(-50%, -50%) rotate(-180deg);
          transform: translate(-50%, -50%) rotate(-180deg);
}
.vertical-gallery .vertical-content {
  position: absolute;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.vertical-gallery .vertical-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  -webkit-transition: all .3s linear;
  transition: all .3s linear;
}
